import { mapActions } from 'vuex';
import MapMarker from 'vue-material-design-icons/MapMarker'; // import AccountMultiplePlus from 'vue-material-design-icons/AccountMultiplePlus'

import Send from 'vue-material-design-icons/Send';
import { Button } from '@nextcloud/vue';
import moment from '@nextcloud/moment';
import Loader from './Loader.vue';
import CollaboratorsSelectionForm from './CollaboratorsSelectionForm.vue';
export default {
  name: 'AlbumForm',
  components: {
    Button,
    MapMarker,
    // AccountMultiplePlus,
    Send,
    Loader,
    CollaboratorsSelectionForm
  },
  props: {
    album: {
      type: Object,
      default: null
    },
    displayBackButton: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      showCollaboratorView: false,
      albumName: '',
      albumLocation: '',
      loading: false
    };
  },

  computed: {
    editMode() {
      return this.album !== null;
    }

  },

  mounted() {
    if (this.editMode) {
      this.albumName = this.album.basename;
      this.albumLocation = this.album.location;
    }

    this.$nextTick(() => {
      this.$refs.nameInput.focus();
    });
  },

  methods: { ...mapActions(['createAlbum', 'renameAlbum', 'updateAlbumLocation']),

    submit() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      if (this.albumName === '' || this.loading) {
        return;
      }

      if (this.editMode) {
        this.handleUpdateAlbum(collaborators);
      } else {
        this.handleCreateAlbum(collaborators);
      }
    },

    async handleCreateAlbum() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      try {
        this.loading = true;
        const album = await this.createAlbum({
          album: {
            basename: this.albumName,
            nbItems: 0,
            location: this.albumLocation,
            lastPhoto: '',
            date: moment().format('MMMM YYYY'),
            collaborators
          }
        });
        this.$emit('done', {
          album
        });
      } finally {
        this.loading = false;
      }
    },

    async handleUpdateAlbum() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      try {
        this.loading = true;
        let album = { ...this.album
        };

        if (this.album.basename !== this.albumName) {
          album = await this.renameAlbum({
            currentAlbumName: this.album.basename,
            newAlbumName: this.albumName
          });
        }

        if (this.album.location !== this.albumLocation) {
          album.location = await this.updateAlbumLocation({
            albumName: this.albumName,
            newLocation: this.albumLocation
          });
        }

        this.$emit('done', {
          album
        });
      } finally {
        this.loading = false;
      }
    },

    back() {
      this.$emit('back');
    }

  }
};